* {
  font-family: 'Muli', sans-serif;
}
.Story-container {
  display: -ms-flexbox;
  display: flex;
  padding-top: 10px;
  background: -webkit-gradient(
    linear,
    left top, left bottom,
    from(rgba(188, 192, 193, 0)),
    to(rgba(188, 192, 193, 1))
  );
  background: -webkit-linear-gradient(
    top,
    rgba(188, 192, 193, 0),
    rgba(188, 192, 193, 1)
  );
  background: -o-linear-gradient(
    top,
    rgba(188, 192, 193, 0),
    rgba(188, 192, 193, 1)
  );
  background: linear-gradient(
    to bottom,
    rgba(188, 192, 193, 0),
    rgba(188, 192, 193, 1)
  );
}

.Story-title {
  -ms-flex: 1 1;
      flex: 1 1;
  color: black;
  font-family: 'Muli', sans-serif;
}

.Story-thumbnail {
  /*flex: 1;*/
  padding: 5px;
  border-radius: 6px;
}

.Story-author {
  -ms-flex: 1 1;
      flex: 1 1;
  padding-top: 45px;
  font-family: 'Muli', sans-serif;
}

.Story-url {
  color: black;
  float: left;
}

.Story-image {
  float: left;
  padding: 0 10px 10px 10px;
}
.Story-button {
  height: 40px;
  width: 75px;
  background: #2dc0f0;
  border-radius: 8px;
  margin: 30px 10px 0 0;
  font-family: 'Muli', sans-serif;
  font-size: 13px;
}
.Story-button:active {
  background-color: pink;
}
.Story-button:visited {
  background-color: pink;
}



